<script>
export default {
  data() {
    return {
      input: "",
      show2: false,
      colorCode: "",
      i: document.getElementById("copy-for-inp"),
      copyStrMsg: {
        CopFnc_: t => {
          this.i.focus(function() {
            document.activeElement.blur();
          });
          i.value = t;
          i.select();
          document.execCommand("copy");
          return;
        },
      }
    };
  },
  mounted() {
    // this.copyStrMsg.CopFnc_();
    this.capture();

    // let input = document.getElementById("copy-for-inp");
    // input.focus(function() {
    //   document.activeElement.blur();
    // });
  },
  methods: {
    onHandleClick($e) {
      this.copyInp($e.target.style.background)
    },
    copyInp(text) {
  let input = document.getElementById("copy-for-inp");
  input.focus(function() {
    document.activeElement.blur();
  });
  input.value = text;
  input.select();
  document.execCommand("copy");
},
    capture() {
      //
      function copyInp(text) {
        let input = document.getElementById("copy-for-inp");
        input.focus(function() {
          document.activeElement.blur();
        });
        input.value = text;
        input.select();
        document.execCommand("copy");
      }
      //todo
      let button = document.getElementsByTagName("button");
      for (let index = 0; index < button.length; index ++) {
        //
        button[index].addEventListener("touchstart", () => {
          // alert('yes')
          let copyStr = button[index].getAttribute("style").slice(12, -1);
          document.getElementById("colorMarsk1-p").style.background = copyStr;
          document.body.style.background = copyStr;
        });
        //
        button[index].addEventListener("mouseover", function() {
          let copyStr = button[index].getAttribute("style").slice(12, -1);
          document.body.style.background = copyStr;
        });
        //
        button[index].addEventListener("click mouseover", function() {
          // button[index].setAttribute('@click','show2 = !show2')
          let colorMarsk1 = document.getElementById("colorMarsk1");
          colorMarsk1.style.opacity = "1";
          colorMarsk1.style.visibility = "unset";
          setTimeout(function() {
            colorMarsk1.style.opacity = "0";
            colorMarsk1.style.visibility = "hidden";
          }, 1500);

          let copyStr = button[index].getAttribute("style").slice(12, -1);
          document.getElementById("colorMarsk1-p").style.background = copyStr;
          document.body.style.background = copyStr;
          this.colorCode = copyStr;
          copyInp(copyStr)
          // this.copyStrMsg.CopFnc_(copyStr);
          let click1v_ = document.getElementById("voiceOfClick1");
          click1v_.play();
        });
      }
      console.log(button.length);
    },
    openMe32() {
      this.$message("");
    }
  }
};
</script>
<style>
* {
  padding: 0;
  margin: 0;
  /* filter: saturate(10%); */
}
html {
  background: #5d3131;
  transition: 0.5s;
}
body {
  transition: 0.8s;
}
#colorMarsk1 {
  /* display: flex; */
  opacity: 0;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  /* font-weight: 1000; */
  /* color: white; */
  position: fixed;
  background: rgba(255, 255, 255, 0.3);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  transition: 0.5s;
}
@keyframes identifier {
}
#colorMarsk1-p {
  line-height: 150px;
  width: 100%;
  /* background: #f1908c; */
  color: white;
}
#padding-main {
  padding: 0;
}
#header {
  margin-top: 5%;
  color: white;
  font-size: 30px;
}
.main-body {
  display: flex;
  flex-direction: column;
}
.main-body-line {
  display: flex;
}
button {
  flex: 1;
  border: 0;
  outline: none;
  height: 120px;
  color: white;
  font-size: 17px;
  text-shadow: 3px 3px 3px 2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
button:hover {
  transform: scale(1.5);
  z-index: 10;
  /* filter: blur(3px); */
}
#copy-for-inp {
  position: fixed;
  top: 10%;
  border: 0;
  background: rgba(0, 0, 0, 0);
  outline: none;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 5px;
  color: white;
  opacity: 0;
}
#el-zoom-in-center {
  position: fixed;
  top: 0;
  /* left: 50%; */
}
.transition-box {
  margin-bottom: 10px;
  width: 100%;
  height: 100px;
  /* border-radius: 4px; */
  background-color: #409eff;
  text-align: center;
  color: #fff;
  padding: 40px 20px;
  box-sizing: border-box;
  margin-right: 20px;
  z-index: 100;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  font-size: 30px;
  position: fixed;
  /* background: rgba(255, 255, 255, 0); */
  top: 40%;
  left: 0;
  /* width: 100%; */
}
</style>



